     nav {\r\n         width: fit-content;\r\n         height: fit-content;\r\n         top: 3.5%;\r\n         right: 3.5%;\r\n         mix-blend-mode: normal;\r\n         background: none;\r\n         filter: drop-shadow(1px 2px 2px var(--text));\r\n      }\r\n      .logo img {\r\n         filter: invert(1);\r\n      }\r\n      .navbar ul {\r\n         display: flex;\r\n         gap: 15px;\r\n      }\r\n\r\n      .navbar a {\r\n         color: var(--bg);\r\n         font-weight: bold;\r\n      }\r\n\r\n      /* メインレイアウト */\r\n      main {\r\n         display: flex;\r\n         max-width: 1200px;\r\n         height: auto;\r\n         margin: 10vmin auto 5vmin;\r\n         gap: 5vmin;\r\n         background: rgba(216, 216, 216, 0.65);\r\n         border-radius: 20px;\r\n      }\r\n\r\n      main h1 {\r\n         margin-top: 4vmin;\r\n      }\r\n\r\n      main a {\r\n         width: fit-content;\r\n         font-size: 1.8rem;\r\n         text-align: left;\r\n         border: 0;\r\n         padding: 0;\r\n         border-bottom: 1px solid var(--side-a);\r\n      }\r\n\r\n\r\n      /*カテゴリー ジャンル*/\r\n      .article-meta {\r\n         line-height: 2.4;\r\n      }\r\n\r\n      .category {\r\n         color: var(--a);\r\n      }\r\n\r\n      .category a {\r\n         line-height: 1;\r\n      }\r\n\r\n      .category::before {\r\n         content: \"Category\";\r\n         font-weight: 600;\r\n         color: var(--text);\r\n      }\r\n\r\n      .genre {\r\n         font-weight: 600;\r\n         color: var(--text);\r\n      }\r\n\r\n      .sidebar {\r\n         flex: 1;\r\n         width: 23vmin;\r\n         position: relative;\r\n         padding: 2vmin;\r\n         border-right: 5px solid var(--text);\r\n      }\r\n\r\n      .sidebar h2 {\r\n         font-size: 2rem;\r\n      }\r\n\r\n      .sidebar a {\r\n         color: var(--side-a);\r\n      }\r\n\r\n      .sidebar a:hover {\r\n         color: var(--text);\r\n      }\r\n\r\n      /*said*/\r\n      .categories {\r\n         position: relative;\r\n         top: 5vmin;\r\n      }\r\n\r\n      .toc {\r\n         position: relative;\r\n         top: 10vmin;\r\n      }\r\n      .toc a {\r\n         font-size: 1.2rem;\r\n      }\r\n      .toc a:active {\r\n         color: var(--text);\r\n      }\r\n      .categories li {\r\n         list-style: circle;\r\n         margin-left: 2.5vmin;\r\n      }\r\n\r\n      .toc li {\r\n         font-weight: bold;\r\n         margin-left: 1.5vmin;\r\n      }\r\n\r\n      .content {\r\n         flex: 3;\r\n         padding: 10rem;\r\n         padding-left: 5rem;\r\n      }\r\n\r\n      .content h2,\r\n      .content h3 {\r\n         width: 100%;\r\n         border-bottom: 1px solid var(--text);\r\n         text-align: left;\r\n         padding: 15vmin 1rem 0;\r\n         margin: 0 auto;\r\n         border-bottom: 1px solid;\r\n         mix-blend-mode: normal;\r\n         background: none;\r\n         position: relative;\r\n         z-index: 1;\r\n      }\r\n      .content h2 span {\r\n         font-size: 3.8rem;\r\n         color: #d5d5d5;\r\n         position: absolute;\r\n         transform: rotate(-10deg) translate(-20%,40%);\r\n         z-index: -1;\r\n      }\r\n\r\n      .content p {\r\n         width: 100%;\r\n         text-align: left;\r\n         padding: 1rem 1rem 0;\r\n         margin: 0 auto;\r\n      }\r\n\r\n      /* 記事カード */\r\n      .article-card {\r\n         display: flex;\r\n         gap: 3vmin;\r\n         padding: 1rem;\r\n         border: 1px solid var(--text);\r\n         border-radius: 5px;\r\n      }\r\n\r\n      .article-card:nth-child(odd) {\r\n         margin: 1vmin auto;\r\n      }\r\n\r\n      .articles {\r\n         margin: 15vmin auto 0;\r\n      }\r\n\r\n      .articles h3,\r\n      .articles h4 {\r\n         font-weight: 800;\r\n         width: 100%;\r\n         padding: 0;\r\n         border: 0;\r\n         mix-blend-mode: normal;\r\n      }\r\n\r\n      .articles h3 {\r\n         font-size: 2.4rem;\r\n         border-bottom: 1px solid;\r\n         margin: 2rem 0 1rem;\r\n      }\r\n\r\n      .article-card img {\r\n         height: 90px;\r\n         z-index: 1;\r\n      }\r\n\r\n      .article-card h4 {\r\n         font-size: 1.4rem;\r\n         margin: 0;\r\n      }\r\n\r\n      .article-card p {\r\n         font-size: 1rem;\r\n         margin: 0;\r\n         padding: 0;\r\n      }\r\n\r\n      .articles a {\r\n         color: var(--side-a);\r\n         border-bottom: 1px solid;\r\n      }\r\n\r\n      main .button {\r\n         display: contents;\r\n         height: auto;\r\n         margin: 0;\r\n         color: var(--side-a);\r\n      }\r\n      main .button::before,\r\n      main .button::after {\r\n         display: none;\r\n      }\r\n\r\n\r\n      /* フッター */\r\n      footer {\r\n         position: relative;\r\n         bottom: 10%;\r\n      }\r\n\r\n      footer p {\r\n         text-align: center;\r\n      }\r\n\r\n      /*ページネーション*/\r\n      .pagination {\r\n         display: flex;\r\n         justify-content: center;\r\n         align-items: center;\r\n         margin-top: 1rem;\r\n      }\r\n\r\n      .pagination button {\r\n         padding: 0.5rem 1rem;\r\n         margin: 0 0.5rem;\r\n         border: none;\r\n         background-color: #007BFF;\r\n         color: white;\r\n         cursor: pointer;\r\n         border-radius: 5px;\r\n      }\r\n\r\n      .pagination button:disabled {\r\n         background-color: #ccc;\r\n         cursor: not-allowed;\r\n      }\r\n\r\n      .pagination span {\r\n         margin: 0 1rem;\r\n      }\r\n\r\n      /*==== modal ======*/\r\n      .modal-overlay {\r\n         background: rgba(25, 25, 25, 0.75);\r\n      }\r\n      .modal {\r\n         background-color: none;\r\n         background: var(--bg);\r\n         padding: 5vmin 15vmin 10vmin;\r\n         width: 75%;\r\n      }\r\n      .modal h3 {\r\n         font-size: 2.4rem;\r\n      }\r\n      .modal ol {\r\n         font-weight: 400;\r\n         padding: 2vmin;\r\n      }\r\n      .modal .in-text {\r\n         width: 80%;\r\n         margin: 0 auto;\r\n         padding: 5vmin;\r\n      }\r\n\r\n      .modal li,\r\n      article li {\r\n         font-size: 1.6rem;\r\n         font-weight: 400;\r\n         line-height: 2;\r\n         padding: 0.5vmin;\r\n         border-bottom: 1px solid;\r\n         margin-bottom: 2rem;\r\n      }\r\n\r\n      ol,\r\n      .modal li {\r\n         text-align: left;\r\n         font-weight: 600;\r\n      }\r\n\r\n      .modal p {\r\n         color: var(--text);\r\n         text-shadow: none;\r\n      }\r\n\r\n      .modal a {\r\n         color: var(--side-a);\r\n         padding: 1vmin;\r\n      }\r\n      .modal a:hover {\r\n         color: var(--accent);\r\n      }\r\n\r\n      blockquote {\r\n         font-size: 1.6rem;\r\n         padding: 0 1.5vmin;\r\n         border-left: 5px solid var(--text);\r\n      }\r\n\r\n\r\n\r\n      /*===================== Blog =====================*/\r\n   /* メインレイアウト */\r\n   .blog {\r\n      display: flex;\r\n      flex-wrap: wrap;\r\n      max-width: 300px;\r\n      height: auto;\r\n      margin: 10vmin auto 5vmin;\r\n      gap: 5vmin;\r\n      /*background: rgba(186, 186, 186, 0.76);*/\r\n      background: var(--bg);\r\n   }\r\n   .sidebar {\r\n      flex: none;\r\n      padding: 0;\r\n      border-right: 0;\r\n   }\r\n\r\n   .sidebar h2 {\r\n      font-size: 2rem;\r\n   }\r\n\r\n   .sidebar a {\r\n      color: var(--side-a);\r\n   }\r\n\r\n   .sidebar a:hover {\r\n      color: var(--text);\r\n   }\r\n\r\n   /*said*/\r\n   .categories {\r\n      margin-top: 0;\r\n      top: 15vmin;\r\n   }\r\n\r\n   .toc h2 {\r\n      margin-top: 5vmin;\r\n   }\r\n\r\n   .toc {\r\n      width: 50%;\r\n      margin-top: 0;\r\n      bottom: 0;\r\n   }\r\n   .toc a {\r\n      font-size: 1.2rem;\r\n   }\r\n   .toc a:active {\r\n      color: var(--text);\r\n   }\r\n   .categories li {\r\n      list-style: circle;\r\n      margin-left: 2.5vmin;\r\n   }\r\n\r\n   .toc li {\r\n      font-weight: bold;\r\n      margin-left: 1.5vmin;\r\n   }\r\n   .content {\r\n      padding: 3rem;\r\n      padding-left: 0;\r\n   }\r\n\r\n   .content h2,\r\n   .content h3 {\r\n      width: 100%;\r\n      border-bottom: 1px solid var(--text);\r\n      text-align: left;\r\n      padding: 15vmin 1rem 0;\r\n      margin: 0 auto;\r\n      border-bottom: 1px solid;\r\n      mix-blend-mode: normal;\r\n      background: none;\r\n      position: relative;\r\n      z-index: 1;\r\n   }\r\n   .content h2 span {\r\n      font-size: 3.8rem;\r\n      color: #d5d5d5;\r\n      position: absolute;\r\n      transform: rotate(-10deg) translate(-20%,40%);\r\n      z-index: -1;\r\n   }\r\n\r\n   .content p {\r\n      width: 100%;\r\n      text-align: left;\r\n      padding: 1rem 1rem 0;\r\n      margin: 0 auto;\r\n   }\r\n\r\n   /* 記事カード */\r\n   .article-card {\r\n      flex-direction: column;\r\n      gap: 3vmin;\r\n      padding: 1rem;\r\n      border: 1px solid var(--text);\r\n      border-radius: 5px;\r\n   }\r\n\r\n   .articles {\r\n      margin: 20vmin auto 0;\r\n   }\r\n\r\n   .articles h3,\r\n   .articles h4 {\r\n      font-weight: 800;\r\n   }\r\n\r\n   .articles h3 {\r\n      margin: 2rem 0 1rem;\r\n   }\r\n\r\n   .article-card img {\r\n      width: 100%;\r\n      height: 70px;\r\n   }\r\n\r\n   .article-card h4 {\r\n      font-size: 1.4rem;\r\n      margin: 0;\r\n   }\r\n\r\n   .article-card p {\r\n      font-size: 1rem;\r\n      margin: 0;\r\n      padding: 0;\r\n   }\r\n\r\n   .articles a {\r\n      color: var(--side-a);\r\n      border-bottom: 1px solid;\r\n   }\r\n\r\n   main .button {\r\n      display: contents;\r\n      height: auto;\r\n      margin: 0 auto;\r\n      color: var(--side-a);\r\n   }\r\n   main .button::before,\r\n   main .button::after {\r\n      display: none;\r\n   }\r\n   /*===================== Blog =====================*/"],"sourceRoot":""}]);
